body {
  background-color: #f4f1f1;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin: 80px auto 20px;
  display: flex;
}

.photo-card {
  width: calc(20% - 4px);
  min-width: 300px;
  background-color: #faf9f9;
  border: 2px solid #4c89ae;
  border-radius: 8px;
  padding: 10px;
}

.photo-box {
  height: 280px;
}

.photo-image {
  width: 100%;
  margin: 0 0 auto;
  display: block;
}

.info {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

.info-item {
  margin: 4px 0;
}

.info-item:not(:last-child) {
  margin-right: 10px;
}

.search-form {
  width: 100vw;
  text-align: center;
  background: #f4f2f2;
  margin: 0 auto;
  padding: 12px;
  position: fixed;
  top: 0;
}

.form-input {
  min-width: 160px;
  height: 12px;
  border: 2px solid #85c0b3;
  border-radius: 4px;
  padding: 10px;
}

button {
  min-width: 80px;
  cursor: pointer;
  font: bold;
  border: 2px solid #85c0b3;
  border-radius: 4px;
  padding: 8px;
}

button:hover, button:focus {
  border: 2px solid rgba(56, 214, 16, .51);
}

.form-button {
  background: rgba(229, 230, 225, .925);
}

.load-more {
  width: 200px;
  color: #fff;
  cursor: pointer;
  background-color: rgba(30, 50, 117, .686);
  border: 2px solid rgba(30, 50, 117, .83);
  border-radius: 12px;
  margin: 0 auto;
  padding: 8px 0;
  display: block;
}

.load-more:disabled {
  background-color: #7588c5;
  border-color: #596dad;
}

.load-more.hidden {
  display: none;
}

